<template>
  <div class="nav-bar">
    <div class="left" @click="goback"><slot name="left">
      <img src="~assets/images/left.png" alt="">
    </slot></div>
    <div class="center"><slot>EWShop</slot></div>
    <div class="right"><slot name="right"></slot></div>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'

export default {
  name: "NavBar",

  setup(){
    const router = useRouter();

    const goback = ()=>{
      router.go(-1);  // 回到上一个页面
    }

    return{
      goback,
    }

  }
}
</script>

<style scoped>

.nav-bar{
  display: flex;  /* 弹性盒布局 */
  background: var(--color-tint);
  color: #FFFFFF;
  position: fixed;  /* 位置固定 */
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;  /* 堆叠顺序，高堆叠顺序避免其被遮挡 */
  height: 45px;
  line-height: 45px;  /* 行高 */
  text-align: center;  /* 文本居中 */
  box-shadow: 0 2px 0 rgba(100, 100, 100, 0.1);
}

.left, .right{
  width: 60px;
}
.left img{  /* left 类中的 img */
  width: 45px;
  padding: 12px;
}
.center{
  flex: 1;  /* 中间占满整部分区域 */
}

</style>